body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

ul.layers {
  padding-left : 0
}

li.layer {
  list-style: none;
  display : grid;
  grid-template-columns : 5fr 1fr;
  cursor : pointer;
  border : 1px solid white;
}

li.layer:hover {
  border-color : green;
}

li.layer.selected {
  border-color : black;
}

li.bad {
  background-color : #FFAAAA;
}

li.good {
  background-color : #AAFFAA;
}

.add-layer {
  display : grid;
  grid-template-columns: 1fr 1fr;
}

.good-button {
  background-color : #AAFFAA;
}

.bad-button {
  background-color : #FFAAAA;
}

.layer-detail {
  display : grid;
  grid-template-columns : 1fr 1fr;
  grid-template-rows : repeat(5, 1.5em), 7em;
}

.layer-detail .notes {
  grid-column: span 2;
}

.serialize-store {
  width : 100%
}

.App {
  display : grid;
  grid-template-columns: 1fr 1fr;
  width : 700px;
}

.App .layer-detail {
  width : 300px;
  grid-column : 2;
  grid-row : 1 / span 3;
  border : 1px solid blue;
}

.App .layer-detail:empty {
  border : 0
}

